.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0);
    transition: background 0.5s ease;
  }
  .cards1:hover .overlay {
    display: block;
    background: rgba(0, 0, 0, .3);
  }

  .cards1:hover .laptopTitle {
    top: 90px;
  }
  
  .btn {
   
    left:0;
    /* top: 180px; */
    text-align: center;
    /* opacity: 0; */
    transition: opacity .35s ease;
  }
  .bttttt{
    width: 200px;

  }
  .wrapper {
    text-align: center;
}

.button {
    /* position: absolute; */
}
  
  .button a {
    width: 200px;
    padding: 12px 48px;
    text-align: center;
    color: black;
    border: solid 2px black;
    z-index: 1;
  }
  
  .cards1:hover .button {
    opacity: 1;
  }
  
  .btn {
    display: none;
}

.cards1:hover + .btn, .btn:hover {
    display: inline-block;
}
.cards1:hover .btn {
    opacity: 1;
  }
