body{
    background: linear-gradient(black, whitesmoke);
    /* background-image: url("./Superb_HD_Laptop_Background_Wallpaper.jpg"); */
    /* background-color: blue; */
    /* background-repeat: no-repeat; */
    /* width: 100%; */
    /* background-position: center; */
    /* background-size: cover; */
    /* height: -webkit-fill-available; */
    height: 200px;
    width: 100%;
    overflow-x: hidden;
    /* overflow-y: hidden; */
    background-attachment: fixed;

}
.form{
    /* background-color: whitesmoke; */
    color: antiquewhite;
    margin-top: 209px;
    width: 478px;
   margin-left: 406px;
   height:250px;
   padding: 40px;
   color: black;
   border-radius: 30px
   
}
.form-control1{
    background-color: transparent;
    display: block;
    width: 100%;
    height: calc(1.5em + .9rem + 2px);
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: ease-in-out,box-shadow .15s ease-in-out;
}
.Submit{
    /* height: 50px; */
    width: 500px;
    margin-left:100px;
    padding-left:100px
   

}
.btn-primary{
    
        color: #fff;
        background-color: #007bff;
        border-color: #007bff;
        padding-left: 59px;
        margin-left: 80px;
        width: 230px;
    
}

.form-control{
    display: block;
    width: 100%;
    margin-left: 0px;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
.ForgotPasswordDiv{
    margin-top: -50px;
}
.ForgotPassword{
    padding-left: 446px;
}
.SignUp{
    padding-left: 147px;

}
.SignUp:hover a{
    color:whitesmoke;
}
.ForgotPassword:hover a{
    color:whitesmoke;
}


