.NavBar{
    background-color:whitesmoke ;
    width: 100%;
    position:absolute;
    top: 0%;
    overflow: hidden;
    margin-left: 0px;
}
.NavBarList a{
    color: gray
    
}

.MyFilter{
    padding-left: 100PX;

}





ul{
    overflow:hidden;
    }
li{
    display:inline-block;
    padding:20px;

    }
.Account{
        margin-left: 2px;
    
    }

    



   /* ------------------------------------------------------- */
  



