    @media only screen and (max-width: 600px) {
        .NavBar {
          background-color:whitesmoke;
        }
        .Account{
            padding-left: 10px;
        }
        ul{
            overflow:hidden;
            }
            li{
            display:inline-block;
            padding:3px;
        
            }
           
      }
      
      @media all and (min-width:321px) and (max-width: 1600px) {
        .NavBarList {
            color: gray;
            height: 60px;
            font-size: 14px;
            padding-left: 14px;
            margin-top: 18px;
        }
         .NavBar{
             height:70px;
         }
        .Account{
          margin-left: 18px;
        }
       dl,ul,ol{
           margin-top:0px;
           

       }

       ul{
        overflow:hidden;
        }
        li {
            display: inline-block;
            padding: 4px;
        }
       
     
